import axios from "axios";
import { ENDPOINTS } from "../constants/api";
import { useEffect, useState } from "react";
import Modal from "./Modal";
import ReviewForm from "./ReviewForm";

export default function ReviewList({ refreshReviews }) {
  const [reviews, setReviews] = useState([]);
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [editingReview, setEditingReview] = useState(null);
  const [reviewToDelete, setReviewToDelete] = useState(null);

  useEffect(() => {
    const fetchReviews = async () => {
      try {
        const response = await axios.get(ENDPOINTS.GET_REVIEWS);
        setReviews(response.data);
      } catch (error) {
        console.error("Failed to fetch reviews:", error);
      }
    };
    fetchReviews();
  }, [refreshReviews]);

  const openEditModal = (review) => {
    setEditingReview(review);
    setIsModalOpen(true);
  };

  const openDeleteModal = (review) => {
    setReviewToDelete(review);
    setIsModalOpen(true);
  };

  const handleDelete = async () => {
    try {
      await axios.delete(ENDPOINTS.DELETE_REVIEW(reviewToDelete._id));
      setReviews(reviews.filter((r) => r._id !== reviewToDelete._id));
      setIsModalOpen(false);
      setReviewToDelete(null);
    } catch (error) {
      console.error("Failed to delete review:", error);
    }
  };

  return (
    <div className="p-6">
      <h1 className="text-3xl font-bold mb-4">Book Reviews</h1>
      {reviews.length > 0 ? (
        reviews.map((review) => (
          <div key={review._id} className="mb-4 border p-4 rounded-lg">
            <h2 className="text-2xl">{review.bookTitle}</h2>
            <p className="text-gray-700">By {review.author}</p>
            <p className="mt-2">{review.reviewText}</p>
            <p className="mt-2">‚≠ê {review.rating} / 5</p>
            <div className="mt-4">
              <button
                onClick={() => openEditModal(review)}
                className="bg-yellow-500 text-white px-4 py-2 rounded mr-2"
              >
                Edit
              </button>
              <button
                onClick={() => openDeleteModal(review)}
                className="bg-red-500 text-white px-4 py-2 rounded"
              >
                Delete
              </button>
            </div>
          </div>
        ))
      ) : (
        <p>No reviews found.</p>
      )}

      {/* Edit Modal */}
      <Modal
        isOpen={isModalOpen && editingReview}
        onClose={() => setIsModalOpen(false)}
      >
        <ReviewForm
          refreshReviews={refreshReviews}
          editingReview={editingReview}
          setEditingReview={setEditingReview}
          onClose={() => setIsModalOpen(false)}
        />
      </Modal>

      {/* Delete Confirmation Modal */}
      <Modal
        isOpen={isModalOpen && reviewToDelete}
        onClose={() => setIsModalOpen(false)}
      >
        <p className="mb-4">Are you sure you want to delete this review?</p>
        <div className="flex justify-end">
          <button
            onClick={handleDelete}
            className="bg-red-500 text-white px-4 py-2 rounded mr-2"
          >
            Delete
          </button>
          <button
            onClick={() => setIsModalOpen(false)}
            className="bg-gray-300 px-4 py-2 rounded"
          >
            Cancel
          </button>
        </div>
      </Modal>
    </div>
  );
}
